<script>
import store from "../store";
import axios from 'axios';
export default {
    name: 'Consultas Operativas - Login',
	data: () => {
    	return {
      		username: "",
      		password: "",
			msg: "",
    	};
  	},
	methods: {
		async login(){
			try {
				const credentials = {
					"username": this.username,
					"password": this.password
				};
				const response = await axios.post(`${process.env.VUE_APP_API_LOGIN}`, credentials);
				// console.log(response.data.token);
				// console.log(response.data.user);
				const token = response.data.token;
				const user = response.data.user;
				store.dispatch('login', {token, user});
				this.$router.push('/tablero');
			}
			catch(error){
				console.log(error.toJSON());
				if (error.response.status === 500) {
				// La respuesta fue hecha y el servidor respondi con un cdigo de estado
				// que esta fuera del rango de 2xx
				this.msg = "Usuario / Contrasea invalida"
				console.log(error.response.data.error.message);
				console.log(error.response.status);
				console.log(error.response.headers);
				} else if (error.request) {
				// La peticin fue hecha pero no se recibi respuesta
				// `error.request` es una instancia de XMLHttpRequest en el navegador y una instancia de
				// http.ClientRequest en node.js
				console.log(error.request);
				} else {
				// Algo paso al preparar la peticin que lanzo un Error
				console.log('Error', error.message);
				}
				console.log(error.config);
			}
		}
  },
}
</script>

